*{
  margin: 0;
  padding-top: 20px;
  box-sizing: border-box;
}

body{
  font-family: "Nunito", sans-serif;
}

.Hero{
  background-image: url('./Assets/queen.jpg');
  background-size: cover;
  background-position: bottom ;
  transition: 0.4 ease-out;
}


.areyoublind{
  
    border: none;
    margin-left: 90%;
    outline: none;
    border-radius: 10px;
    width: fit-content;
    padding: 15px 10px;
    color: #fff;
    font-size: 16px;
    letter-spacing: 1px;
    background: #ff0055;
    cursor: pointer;
  
}
.areyoublind:hover{
  background-color: #ff3b7cb7;
  color: #000000;
}

 h1{

  color:rgb(218, 177, 177);
  font-size: 50px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
  align-items: center;
  text-align: center;
  position: relative;
  

}

.headeryaar{
  color:rgb(194, 31, 31);
  font-size: 50px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
  align-items: center;
  text-align: center;
  position: relative;
}


.Hero.warm{
  background-image: url('./Assets/queensamundar.jpg');
}

main{
  min-height: 100vh;
   background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75) ); 
  padding: 25px;
}

.search-box{
  width: 100%;
  padding-top: 50px;
 

}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);

  color: #313131;
  font-size: 20px;
  transition: 0.4 ease;
  
}

.search-box .search-bar :focus{
  background-color: rgba(255, 255, 255, 0.6);
}

.location-box .location{
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rbga(50,50,70,0.5); 
}

.location-box .date{
  color: #fff;
  font-size:20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
}
.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin:30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size:102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);

}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}



