* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}


button {
  border: none;
  
    outline: none;
    border-radius: 20px;
    width: fit-content;
    padding: 15px 10px;
    color: #fff;
    font-size: 16px;
    letter-spacing: 1px;
    background: #ff0055;
    cursor: pointer;
    margin: 5px 5px 5px 150px;
}
button:hover{
  background-color: #ff3b7cb7;
  color: #000000;
  transition: ease-in ;
}

.login {
  width: 100%;
  min-height: 100vh;
  padding: 0 20px;
  background: #e9e9e9;
  display: flex;
}

.login .loginContainer {
  padding: 60px;
  border-radius: 10px;
  margin: auto;
  width: 100%;
  max-width: 520px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(
    ellipse at left bottom,
    rgba(22, 24, 47, 1) 0%,
    rgba(38, 20, 72, 0.9) 59%,
    rgba(17, 27, 75, 0.9) 100%
  );
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
}

.login .loginContainer label {
  color: white;
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}

.login .loginContainer input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  letter-spacing: 1px;
}

.login .loginContainer .btnContainer {
  width: 100%;
  padding: 24px 0;
}

.login  p {
  margin: 14px 0 0 0;
  text-align: center;
  color: #fff;
}

.login span {
  width: 100px;
  margin-top: 5px;
  padding:2% ;
  align-items: center;
  border-radius: 5px;
  color: #e9e9e9;
  cursor: pointer;
  
}
.login span:hover{
  color: #ff0055;
}


.login .loginContainer .btnContainer p span:hover {
  color: rgb(0, 255, 221);
}


.login .loginContainer .errorMsg {
  color: red;
  font-size: 16px;
}

.hero {
  
  width: 100%;
  min-height: 100vh;
  background: radial-gradient(
    ellipse at left bottom,
    rgba(22, 24, 47, 1) 0%,
    rgba(38, 20, 72, 0.9) 59%,
    rgba(17, 27, 75, 0.9) 100%
  );
}

.hero nav {
  background: #a81414;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero nav h2 {
  color: rgb(185, 49, 49);
 
}





.hero body {
  background: #ffffff;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero body h2 {
  color:#fff;
  align-items: center;
}

.hero body button {
  width: 100px;
  padding: 8px 0;
  align-items: center;
  border-radius: 5px;
  background:  rgb(110, 8, 8);
}



 

.hero {
  text-align: center;
}

.hero-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .hero-logo {
    animation: hero-logo-spin infinite 20s linear;
  }
}

